import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";

import {fetchDocuments} from "../actions/documents";
import {createGetDocumentsForTypesSelector} from "../selectors";
import MulticartDocumentLink from "./MulticartDocumentLink";
import {Section, Toggle} from 'eshop/modules/core/components/ui/Expander';

class MulticartDocumentsListComponent extends Component {

    getSortedDocuments(documents) {
        return documents
            .map(document => {
                let key = document.documentCode + (document.bundleNo ? "_" + document.bundleNo : '');
                return <MulticartDocumentLink key={key} {...document} />
            });
    }

    render() {
        let documents = this.getSortedDocuments(this.props.documents);
        const toggleHeader = <h3 className="h4 opl-section__heading">{this.props.title}</h3>
        const sectionHeader = <div className="l-row" ><Toggle variant="arrow" header={toggleHeader} className="l-col l-col-12 " triggerClassName="opl-document__section__trigger"><span className="opl-section__text-show">rozwiń sekcję Umowy</span><span className="opl-section__text-hide">zwiń sekcję Umowy</span></Toggle></div>

        return (
           <Section header={sectionHeader}
                    id={"document-component-subsection-" + this.props.sectionKey}
                    variant="section"
                    className={"opl-document__section" + (this.props.documents.length === 0 ? ' u-hide' : '')}
                    contentClassName="opl-document__section__content">
               <div className="u-padding-left-l-xl u-small-no-padding-l u-padding-m">
                   <ul>
                       {documents}
                   </ul>
               </div>
           </Section>
       )
    }
}

MulticartDocumentsListComponent.propTypes = {
    title: PropTypes.string,
    types: PropTypes.array
};

const makeMapStateToProps = () => {
    const getDocumentsForTypes = createGetDocumentsForTypesSelector();

    return (state, props) => ({
        documents: getDocumentsForTypes(state, props)
    });
};

const mapDispatchToProps = dispatch => ({
    fetchDocuments: () => dispatch(fetchDocuments())
});

export default connect(makeMapStateToProps, mapDispatchToProps)(MulticartDocumentsListComponent);